import returns from '../common/returns.vue'
export default{
	components:{
		returns
	},
	data(){
		return{
			title:'我的测评',
			numer :'',
			id:0,
			questionList:[],
			data:[],
			item:[],
			index:0,
		}
	},
	methods:{
		num(id){
			this.numer = id;
		},
		
		net(index){
			var num = this.questionList.length - 1;
			if(this.numer ==''){
				uni.showToast({
					icon:'none',
					title:'请选择您的答案'
				})
				return
			}
			if(index < num){
				
				this.data.push(this.numer)// += this.numer + ',';
				this.numer =''
				var id = index + 1;
				this.index = id;
				console.log(id)
				this.item = this.questionList[id];
			}else{
				this.data.push(this.numer);
				let data = JSON.stringify(this.data)
				uni.redirectTo({
					url:'./result?id='+this.id+'&data='+data
				})
				
				
			}
			/* var data = {
				'user_id': this.$store.state.user.id,
				'paper_id':this.questionList[index].id,
				''
			}; */
		}
	},
	onLoad(e){
		this.id = e.id;
		// console.log(e)
		this.service.entire(this,'post',this.APIconfig.api_root.subindex.s_test,{
			id:e.id
		},function(self,res){
			// console.log(res)
			console.log(res.data.question)
			self.questionList = res.data.question
			self.item = res.data.question[0];
		})
	}
}
